(function() {
    'use strict';

    angular
        .module('frontend')
        .service('SectionMoveService', SectionMoveService);
    SectionMoveService.$inject = ['$log','SectionListService','SectionAddService']

    /** @ngInject */
    function SectionMoveService($log,SectionListService,SectionAddService) {
        var idcnt = 3
        var defaultSectionProp = {id:'',sectionName:'',position:'',relativeSection:'',OrderIndex:'',headerSectionOpeartion:['edit','delete','move'],fieldOperation:[],field:[]}
        var selectedSectionList = [
            {id: '0', name: "bio", alias: "bio"},
            {id: '1', name: "edu",alias: "edu"}
        ]
        this.editSection = editSection
        function editSection(notFormatedsection){
            var oldsectionName = notFormatedsection.items.sectionName
            var section =JSON.parse(JSON.stringify(notFormatedsection.items)) // notFormatedsection.items
            section.position = notFormatedsection.order
            section.relativeSection = notFormatedsection.section
            section.sectionName = notFormatedsection.sectionName
            convertAddInAvailableRelativeSection(section,oldsectionName)

            var list = SectionListService.getSectionList()
            // var convertProperSection = defaultSectionPropAdd(section)

            //find position delete from there modify the list
            var index = findPosition(section,list)
            var organizedList= removefromList(index,list)



            console.log(organizedList,section)

            //find the correct position insert there
            var findIndexForInsertion = SectionAddService.findIndexOFNewSection(section,organizedList);
            // var findIndexForInsertion = findIndexForInsertionFn(section,organizedList)
            organizedList = SectionListService.addInList(organizedList,section,findIndexForInsertion)
            console.log(organizedList)
            SectionListService.setSectionList(organizedList)
        }

        function convertAddInAvailableRelativeSection(obj,oldName){
            var sectionName = obj.sectionName
            var listObj = SectionListService.getAvailableRelativeSection()
            if(listObj.hasOwnProperty(oldName))
                delete  listObj[oldName];
            var res = {}
            res.id = 0
            res.name = sectionName
            res.alias = sectionName
            listObj[sectionName] = res;
            // changeOldSectionName(sectionName,oldName)
        }

        function changeOldSectionName(newName,oldName){
            var list = SectionListService.getSectionList()
            var listObj = SectionListService.getAvailableRelativeSection()
            list.forEach(function(obj){
                if(obj.sectionName==oldName){
                    obj.sectionName = newName
                }

                if(obj.relativeSection.alias==oldName){
                    obj.relativeSection = listObj[newName]
                }

            })
            SectionListService.setSectionList(list)


        }


        function removefromList(index,list){
            var availableOrderPosition = SectionListService.getavailableOrderPosition()
            var availableRelative = SectionListService.getAvailableRelativeSection()
            switch (index){
                case 0:
                    if(list[1].position.name=="down") {
                        list[1].position = availableOrderPosition['above']
                        if(list.length==2)
                            list[1].relativeSection = availableRelative[list[0].sectionName]
                        else
                            list[1].relativeSection = availableRelative[list[2].sectionName]
                    }
                    //delete first element
                    break;
                case list.length-1:
                    var ind = list.length-2
                    //delete last element
                    if(list[ind].position.name=="above") {
                        list[ind].position = availableOrderPosition['down']
                        if(list.length==2)
                            list[ind].relativeSection = availableRelative[list[ind+1].sectionName]
                        else
                            list[ind].relativeSection = availableRelative[list[ind-1].sectionName]
                    }
                    break
                default:
                    if((list.length>2 )&& (list[i-1].position.name=="above") && (list[i+1].position.name=="down")){
                        list[i-1].relativeSection = availableRelative[list[i+1].sectionName]
                        list[i+1].relativeSection = availableRelative[list[i-1].sectionName]
                    }
                    else if (list[i-1].position.name=="above"){
                        list[i-1].relativeSection = availableRelative[list[i+1].sectionName]

                    }
                    else if (list[i+1].position.name=="down"){
                        list[i+1].relativeSection = availableRelative[list[i-1].sectionName]
                    }
            }
            list.splice(index,1)
            return list;

        }

        function findIndexForInsertionFn(editsection,list){


        }

        function findPosition(editsection,list){
            var index = 0;
            list.forEach(function(section,i){
                if(editsection.sectionName==section.sectionName)
                    index = i
            })
            return index;
        }

        function defaultSectionPropAdd(section){
            var res = {}
            for(var prop in defaultSectionProp){
                if(defaultSectionProp.hasOwnProperty(prop)){
                    switch(prop){
                        case 'id': res[prop] = idcnt++;break;
                        case 'sectionName' : res[prop] = section[prop]; break;
                        case 'position' : res[prop] = section.order.alias; break;
                        case 'relativeSection': res[prop] = section.section.alias;break
                        default :
                            if (typeof defaultSectionProp[prop] !=="object") res[prop] = '' ;
                            else{
                                res[prop] = []
                                defaultSectionProp[prop].forEach(function(opearation1){
                                    if(prop=='headerSectionOpeartion'){
                                        var obj = {}
                                        obj.operation = opearation1
                                        res[prop].push(obj)
                                    }else {
                                        res[prop].push(opearation1)
                                    }
                                })
                            }
                            break;
                    }
                }
            }
            $log.debug(res)
            return res
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('frontend')
        .service('SectionListService', SectionListService);
    SectionListService.$inject = []

    /** @ngInject */
    function SectionListService() {
        var availableOrderPosition =  {
            above:{id: '0', name: "above", alias: "Above"},
            down:{id: '1', name: "down", alias: "Down"}
        }


        var availableRelativeSection = {
            BIODATA:{id: '0', name: "BIODATA", alias: "BIODATA"},
            EDUCATION:{id: '0', name: "EDUCATION", alias: "EDUCATION"}
        }


        var sectionList = [

            {
                id:1,
                sectionName:'BIODATA',
                description: 'HTML enhanced for web apps!',
                logo: 'angular.png',
                position:{id: '0', name: "above", alias: "Above"},
                relativeSection: {id: '1', name: "EDUCATION", alias: "EDUCATION"},
                OrderIndex:2,
                headerSectionOpeartion  :[
                    {operation:'edit'},
                    {operation:'delete'},
                    {operation:'move'}
                ],
                fieldOperation:['add-field','create field group'],
                field:[
                    {field1:{id:4,name:'profile'}},
                    {field2:{id:4,name:'signup'}}
                ]
            },
            {
                id:2,
                sectionName:'EDUCATION',
                description: 'HTML enhanced for web apps!',
                logo: 'angular.png',
                position:{id: '1', name: "down", alias: "Down"},
                relativeSection:{id: '0', name: "BIODATA", alias: "BIODATA"},
                OrderIndex:1,
                headerSectionOpeartion:[{operation:'default'}],
                fieldOperation:['add-field','create field group'],
                field:[
                    {field1:{id:5,name:'profile'}},
                    {field2:{id:6,name:'signup'}}
                ]
            }
        ];
        window.sectionList = sectionList
        this.getAvailableRelativeSection = getAvailableRelativeSection;
        this.getavailableOrderPosition = getavailableOrderPosition
        this.organizeList = organizeList
        this.getSectionList = getSectionList
        this.addInList = addInList
        this.arrayDataInOrder = arrayDataInOrder
        this.setSectionList = setSectionList
        /**
         * this function return all availbale section data
         * it will query the data base and return the json
         * @returns {*[]}
         */
        function getSectionList() {
            // arrayDataInOrder()
            return sectionList;
        }
        function setSectionList(newList){
            sectionList = JSON.parse(JSON.stringify(newList))

        }

        function getAvailableRelativeSection(){
            return availableRelativeSection;
        }
        function setAvailableRelativeSection( newList){
            availableRelativeSection  = JSON.parse(JSON.stringify(newList))

        }
        function getavailableOrderPosition(){
            return availableOrderPosition;
        }


        /**
         * sort the data when we want to appear same section continous
         */
        function arrayDataInOrder() {
            sectionList.sort(relativeSectionOrder)

            console.log(sectionList)
        }
        function relativeSectionOrder(section1,section2){
            var res = -1;
            if(section1.sectionName == section2.sectionName)
                res =  -1;
            else if(section1.sectionName== section2.relativeSection.alias){
                if(section1.position.name=="down")
                    res = +1;
                else
                    res = -1;
            }
            return res
        }

        /**
         * insert new section object according to its position change another object position in list
         * @param newsection
         */
        function organizeList(newsection,index,sectionList){
            //find the index where we have to insert
            var defaultIntialOjectIndex
            var i
            switch(index){
                case 0:
                    newsection.position=availableOrderPosition.above;
                    newsection.relativeSection =  availableRelativeSection[sectionList[0].sectionName]
                    break;
                case sectionList.length:
                    newsection.position=availableOrderPosition.down;
                     newsection.relativeSection =  availableRelativeSection[sectionList[sectionList.length-1].sectionName]
                    break
                default:

                    //we have to insert below to someone------ may be two change -- based on condition
                     if(newsection.position.name=="down"){
                         i = index-1
                        //change both
                        if((sectionList[i].position.name=="above") &&(sectionList[i+1].position.name=="down") ){
                            sectionList[i].relativeSection = availableRelativeSection[newsection.sectionName]
                            sectionList[i+1].relativeSection = availableRelativeSection[newsection.sectionName]
                        }
                        else if((sectionList[i].position.name=="above"))
                            sectionList[i].relativeSection = availableRelativeSection[newsection.sectionName]
                        else if(sectionList[i+1].position.name=="down")
                            sectionList[i+1].relativeSection = availableRelativeSection[newsection.sectionName]
                    }
            //we have to insert above to someone------ may be two change -- based on condition
                    else{
                         i = index 
                        if((sectionList[i].position.name=="above") &&(sectionList[i-1].position.name=="down") ){
                            sectionList[i].relativeSection = availableRelativeSection[newsection.sectionName]
                            sectionList[i-1].relativeSection = availableRelativeSection[newsection.sectionName]
                        }
                        else if((sectionList[i].position.name=="above"))
                            sectionList[i].relativeSection = availableRelativeSection[newsection.sectionName]
                        else if((sectionList[i-1].position.name=="down"))
                            sectionList[i-1].relativeSection = availableRelativeSection[newsection.sectionName]
                    }
            break
            }
            return sectionList;
        }

        function addInList(sectionList,newsection,index){
            var tempArray = []
            switch(index){
                case 0:sectionList.unshift(newsection);break;
                case sectionList.length: sectionList.push(newsection);break;
                default:
                    for(var j=0;j<index;j++) {
                        tempArray.push(sectionList[j])
                    }
                    tempArray.push(newsection)
                    for(j=index;j<sectionList.length;j++) {
                        tempArray.push(sectionList[j])
                    }
                    sectionList = tempArray //  JSON.parse(JSON.stringify())
                    break;
            }
            return sectionList;
        }


    }

})();

